@import '../../global';

.homepage{
    display: grid;
    grid-template-columns: auto auto;
    justify-content: center;
    align-items: center;
    height: 100vh;
    width: auto;
    top: 0;
    right: 0;
    bottom: 0;
    .home-img{
        display: flex;
        justify-content: center;
        align-items: center;
        img{
            position: relative;
            width: 70%;
            height: auto;
        }
    }
    .body{
        display: flex;
        flex-direction: column;
        padding: 1rem;
        h1{
            color: $secondary;
        }
        p{
            color: whitesmoke;
        }
        button{
            padding: 12px 1.5rem;
            background-color: $secondary;
            border-radius: 10px;
            color: $primary;
            cursor: pointer;
            border: none;
            font-size: 16px;
            font-weight: 700;
            &:hover{
            background-color: darken($secondary, 20);
            }
        }
    }
}
@media only screen and (max-width: 800px){
    .homepage{
        grid-template-columns: auto;
    }
}