@import '../../global';

.error-page {
  display: flex;
  align-items: center;
  height: 100vh;
  .error-wrapper {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    padding: 2rem;
    .error-img {
        display: flex;
        justify-content: center;
        align-items: center;
      img {
        position: relative;
        width: 90%;
        height: auto;
      }
    }
    .error-body {
      text-align: center;
      .heading {
        color: #f9a826;
        font-size: 3rem;
        font-weight: 700;
        margin: 1.5rem;
      }
      .sub-heading {
        color: rgba(216, 216, 216, 0.829);
        font-size: 18px;
        font-weight: 600;
        span {
          color: #00bfa6;
        }
      }
      .text {
        color: rgba(216, 216, 216, 0.603);
        font-size: 16px;
        span{
        color: rgb(218, 218, 218);
        }
      }
      .goback{
          padding: 12px 2rem;
          color: $primary;
          background-color: #f9a826;
          border-radius: 8px;
          border: none;
          cursor: pointer;
          font-size: 16px;
          font-weight: 600;
          margin: 8px;
          &:hover{
            background-color: darken(#f9a826, 10);
          }
      }
      .reload{
        padding: 12px 2rem;
        color: #f9a826;
        background-color: $primary;
        border-radius: 8px;
        border: 2px solid #f9a826;
        cursor: pointer;
        font-size: 16px;
        font-weight: 600;
        margin: 8px;
        &:hover{
          color: darken(#f9a826, 10);
        }
    }
    }
  }
}
