.notes {
  color: #fff;
  .nav-notes {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    height: 70px;
    align-items: center;
    .action-page {
      display: flex;
      flex-direction: row;
      grid-gap: 1rem;
    }
  }
  .heading {
    
    h1 {
      font-size: 36px;
      font-weight: 600;
      line-height: 2rem;
    }
    .date-note {
      color: rgba(255, 255, 255, 0.6);
      display: flex;
      flex-direction: row;
      justify-content: left;
      align-items: center;
      p {
        font-size: 14px;
      }
      svg {
        font-size: 14px;
        margin-right: 6px;
      }
    }
  }
  .body-content {
      p {
        text-align: justify;
        font-size: 16px;
        font-family: 'Raleway', sans-serif;
        font-weight: 500;
        line-height: 24px;
        letter-spacing: 0.6px;
      }
  }
}

@media only screen and ( max-width: 800px){
  .notes {
    .heading {
      h1 {
        font-size: 28px;
      }
    }
  }
}