@import "../../global";

.add-modal {
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: rgba(0, 0, 0, 0.685);
  width: 100%;
  height: 100vh;
  position: fixed;
  display: flex;
  justify-content: center;
  align-items: center;
  backdrop-filter: blur(4px);
  .modal-wrapper {
    background-color: rgba(212, 212, 212, 0.158);
    border-radius: $radius-default;
    border: 1px solid $dark-grey;
    padding: 1rem;
    backdrop-filter: blur(20px);
    .form {
      display: flex;
      flex-direction: column;
      width: 100%;
      grid-gap: 1.5rem;
      .MuiOutlinedInput-input {
        color: rgba(255, 255, 255, 0.849);
        border-radius: $radius-default;
      }
      .MuiFormLabel-root {
        color: $secondary;
      }
      textarea {
        background-color: $primary;
        color: rgba(255, 255, 255, 0.76);
        padding: 12px;
        border-color: $secondary;
        border-radius: $radius-default;
        font-size: 1rem;
        &:focus {
          outline: 2px solid $secondary;
          border-radius: $radius-default;
        }
      }
      .btn-modal {
        display: flex;
        flex-direction: row;
        width: 100%;
        button {
          padding: $padding-btn;
          font-size: 1rem;
          font-weight: 600;
          border-radius: $radius-default;
          cursor: pointer;
          &:hover {
            transform: translateY(-4);
            z-index: 10;
          }
        }
        .btn-submit-modal {
          background-color: $secondary;
          width: 70%;
          color: $primary;
          border: none;
          &:hover {
            background-color: darken($secondary, 20);
          }
          &:active {
            transform: translateY(0);
          }
        }
        .btn-cancle-modal {
          border: none;
          width: 30%;
          background-color: rgba(255, 0, 0, 0);
          color: rgb(204, 28, 28);
        }
      }
    }
  }
}

.value-null {
  color: rgb(199, 48, 48);
  font-size: 1rem;
  font-weight: 500;
  margin: 0;
  padding: 0;
  margin-top: -1rem;
}

@media only screen and( max-width: 500px){
  .add-modal {
    bottom: 0;
    align-items: flex-end;
    .modal-wrapper {
      border-radius: 1rem 1rem 0 0;
    }
  }
}