@import "../../global";

.card-wrapper {
  background-color: $secondary;
  padding: 0.6rem 1rem;
  border-radius: $radius-default;
  height: fit-content;
  margin: 4px;
  background-size: cover;
  &:hover {
    background-color: darken($secondary, 30);
    box-shadow: 4px 6px 8px rgb(17, 17, 17);
    transform: translateY(-4px);
  }
  &:active {
    box-shadow: 3px 4px 8px rgb(17, 17, 17);
    transform: translateY(0px);
  }
  h3 {
    font-size: 18px;
    color: $primary;
    z-index: 2;
    font-weight: 600;
    &:hover {
      text-decoration: underline;
      color: whitesmoke;
    }
  }
  .date {
    color: $primary;
    font-size: 14px;
    display: flex;
    flex-direction: row;
    font-weight: 500;
    align-items: center;
    opacity: 0.4;
    z-index: 2;
    svg {
      font-size: 14px;
      margin-right: 6px;
    }
    .judul-kosong {
      font-size: 24px;
      color: $primary;
      z-index: 2;
      font-weight: 700;
      &:hover {
        text-decoration: underline;
        color: whitesmoke;
      }
    }
    .p-kosong {
      font-size: 1rem;
      color: $primary;
    }
  }
  button {
    background-color: rgba(255, 255, 255, 0);
    border: none;
    color: $primary;
    text-align: left;
    cursor: pointer;
  }
}

.card-wrapper-kosong {
  background-color: $secondary;
  padding: 0.6rem 1rem;
  border-radius: $radius-default;
  height: fit-content;
  margin: 4px;
  background-size: cover;
  svg {
    padding-top: 1rem;
    height: 48px;
    width: 48px;
    color: darken($secondary, 20);
  }
  .judul-kosong {
    margin: 8px 0;
    font-size: 24px;
    color: $primary;
    z-index: 2;
    font-weight: 700;
  }
  .p-kosong {
    font-size: 1rem;
    color: darken($secondary, 30);
  }
  button {
    background-color: rgba(255, 255, 255, 0);
    border: none;
    color: $primary;
    text-align: left;
  }
}

a {
  text-decoration: none;
}
