@import "../../global";

nav {
  .navbar-home {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    .logo-navbar {
      color: white;
      cursor: pointer;
      &:hover{
        color: rgba(255, 255, 255, 0.726);
      }
      h1 {
        letter-spacing: 1.2;
        font-weight: 600;
      }
    }
    .links-navbar {
      display: flex;
      flex-direction: row;
      align-items: center;
      .profile {
        padding: 2px;
        margin-left: 1rem;
        svg {
          height: 24px;
          width: 24px;
          color: rgba(255, 255, 255, 0.726);
          // padding: 1rem;
          cursor: pointer;
        }
      }
    }
  }
}

