@import "../../global";

.myform {
  background-color: $primary;
  position: fixed;
  // top: 0;
  // left: 0;
  // right: 0;
  // bottom: 0;
  height: 100vh;
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  backdrop-filter: blur(10px);
  .form-wrapper {
    background-color: rgba(255, 255, 255, 0.096);
    border-radius: 1rem;
    border: 1px solid rgba(255, 255, 255, 0.13);
    max-width: 400px;
    width: 100%;
    padding: 1.5rem;
    align-items: left;
    box-shadow: 6px 8px 10px rgba(37, 37, 37, 0.678);
    .err-submit-message{
      display: flex;
      flex-direction: row;
      border-radius: 10px;
      padding: 12px;
      align-items: center;
      justify-content: space-between;
      background-color: rgb(230, 86, 86);
      transition: all 0.8s ease-in-out;
      p{
        margin: 0;
        padding: 0;
        color: whitesmoke;
        font-size: 14px;
      }
      .close-err{
        border-radius: 12px;
        height: 24px;
        width: 24px;
        background-color: rgba(255, 255, 255, 0.253);
        cursor: pointer;
        p{
          padding: 2px 8px;
          font-size: 16px;
          color: whitesmoke;
          width: 100%;
        }
      }
    }
    h3 {
      font-size: 1.5rem;
      color: $secondary;
    }
    .form-content {
      label {
        display: flex;
        flex-direction: column;
        margin-top: 1rem;
        color: #c5c5c5;
        input {
          margin-top: 12px;
          padding: 12px;
          color: #c5c5c5;
          border-radius: $radius-default;
          background-color: rgba(255, 255, 255, 0);
          border: 1px solid #c5c5c579;
          &:focus {
            border: 2px solid $secondary;
            outline: none;
          }
        }
      }
      .submit-btn {
        margin: 1rem 0;
        padding: 12px;
        border-radius: $radius-default;
        background-color: $secondary;
        color: $primary;
        border: none;
        width: 100%;
        font-size: 1rem;
        font-weight: 600;
        cursor: pointer;
        &:hover{
        background-color: darken($secondary, 20);
        }
      }
    }
    .a-google {
      button {
        justify-content: center;
        background-color: rgba(255, 255, 255, 0);
        border-radius: $radius-default;
        grid-gap: 12px;
        width: 100%;
        padding: 12px;
        display: flex;
        flex-direction: row;
        align-items: center;
        font-size: 1rem;
        font-weight: 500;
        border: 1px solid #c5c5c579;
        cursor: pointer;
        color: whitesmoke;
        &:hover{
        background-color: whitesmoke;
        border: none;
        color: $primary;
        }
        img {
          height: 1rem;
          width: auto;
        }
      }
    }
    .link {
      padding: 8px 0;
      p {
        color: #dddddda8;
        font-weight: 500;
        align-items: center;
        text-align: center;
        font-size: 14px;
        a {
          font-weight: 600;
          color: $secondary;
          font-size: 1rem;
        }
      }
    }
  }
}

@media only screen and (max-width: 500px) {
  .myform {
    bottom: 0;
    align-items: flex-end;
    .form-wrapper {
      border-radius: 1rem 1rem 0 0;
    }
  }
}
