@import '../../global';

.search {
    svg {
      position: absolute;
      padding: 12px;
      padding-left: 16px;
      height: 18px;
      width: 18px;
    }
    input {
      width: 15rem;
      padding: 12px;
      padding-left: 3rem;
      border-radius: $radius-large;
      border: none;
      background-color: $dark-grey;
      color: whitesmoke;
      font-size: 16px;
      &:focus {
        width: 15rem;
        border: $dark-green;
        transition: 0.5s ease;
      }
    }
  }